	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Haider And Temelin","date":"21 Jan 2002","slug":"/2002-01-21-haider-and-temelin/","tags":"Y2002"},"html":"<p>Guardian: <a href=\"http://www.guardian.co.uk/elsewhere/journalist/story/0,7792,634360,00.html\">Haider plays nuclear power games</a>. Today the petition drive launched by Jörg Haider's FPÖ in against the Czech nuclear plant in Temelin comes to an end. Haider has called on 1 million Austrians to sign the petition, which calls for Austria to block admission of the Czech Republic to the EU until Temelin is shut down. The petition could bring the end of the ruling coalition in Austria, as Premier Schüssel (ÖVP) has already promised not to block Czech admission.</p>"},"next":{"fields":{"title":"Edmund and Sabine","date":"21 Jan 2002","slug":"/2002-01-21-edmund-and-sabine/"}},"previous":{"fields":{"title":"Ladies Cigar Club","date":"21 Jan 2002","slug":"/2002-01-21-ladies-cigar-club/"}}},{"node":{"fields":{"title":"Edmund and Sabine","date":"21 Jan 2002","slug":"/2002-01-21-edmund-and-sabine/","tags":"Y2002"},"html":"<p>I missed Edmund Stoiber's appearance on <a href=\"http://www.sabinechristiansen.de/\">Sabine Christiansen</a> last night. According to Spiegel, <a href=\"http://www.spiegel.de/politik/deutschland/0,1518,178163,00.html\">he blew it</a>. He even addresed her once as 'Frau Merkel'!</p>"},"next":{"fields":{"title":"Daddle","date":"21 Jan 2002","slug":"/2002-01-21-daddle/"}},"previous":{"fields":{"title":"Haider And Temelin","date":"21 Jan 2002","slug":"/2002-01-21-haider-and-temelin/"}}},{"node":{"fields":{"title":"Daddle","date":"21 Jan 2002","slug":"/2002-01-21-daddle/","tags":"Y2002"},"html":"<p><a href=\"http://www.heiferman.com/mcd/\">i was a 20-something dethroned dotcom ceo that went to work the counter at  mcdonald's</a>. And it wasn't a publicity stunt or a joke. (Via <a href=\"http://radio.weblogs.com/0100131/\">William Stratas' Weblog</a>)</p>\n<p>I have a theory about Christopher's babbling. He's noticed that Mama and Papa each have their own language, so he has decided that he needs his own language too. I deciphered one of his words today: 'daddle' means pacifier. When he says it, it usually means he's looking for it.</p>\n<p>I was able to flip my page, even though yesterday's page title was \"empty\". So much for the bug test.</p>\n<p>\"When we let freedom ring, when we let it ring from every village and every hamlet, from every state and every city, we will be able to speed up that day when all of God's children, black men and white men, Jews and Gentiles, Protestants and Catholics, will be able to join hands and sing in the words of the old Negro spiritual, 'Free at last! free at last! thank God Almighty, we are <a href=\"http://www.mecca.org/~crights/dream.html\">free at last</a>!'\"</p>"},"next":{"fields":{"title":"Empty","date":"20 Jan 2002","slug":"/2002-01-20-empty/"}},"previous":{"fields":{"title":"Edmund and Sabine","date":"21 Jan 2002","slug":"/2002-01-21-edmund-and-sabine/"}}}],"pathPrefix":"","first":false,"last":false,"index":941,"pageCount":1101}}

